@import url(https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&display=swap);
@font-face {
  font-family: "SFPro-Text-Light";
  src: local("SFPro-Text-Light"),
    url(/audio-app/static/media/SFProText-Light.8313687a.ttf) format("truetype");
}

@font-face {
  font-family: "SFPro-Text-Regular";
  src: local("SFPro-Text-Regular"),
    url(/audio-app/static/media/SFProText-Regular.90da7ab1.ttf) format("truetype");
}

@font-face {
  font-family: "SFPro-Text-Semibold";
  src: local("SFPro-Text-Semibold"),
    url(/audio-app/static/media/SFProText-Semibold.fee27977.ttf) format("truetype");
}

* {
  box-sizing: border-box;
}

body {
  margin: 0px;
  touch-action: unset;
  overscroll-behavior-y: unset;
  overflow: auto;
  background-color: #ebebeb;
}

.app-container {
  max-width: 375px;
  height: 100vh;
  left: calc(-50vw + 50%);
  right: calc(-50vw + 50%);
  margin-left: auto;
  margin-right: auto;
  box-shadow: 0 0 15px 1px rgba(0, 0, 0, 0.1);
  background: #ffffff;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: "DM Sans";
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  height: 55px;
}

.icon {
  cursor: "pointer";
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  background: #0acf83;
  border-radius: 10px;
  cursor: pointer;
  color: #ffffff;
  font-family: "DM Sans";
  font-style: normal;
  font-weight: bold;
}

.primary {
  font-size: 16px;
  line-height: 20px;
  padding: 20px 10px;
}

.secondary {
  justify-content: space-between;
  font-size: 14px;
  padding: 20px 30px;
}

.auth-container {
  background-image: url(/audio-app/static/media/image-10.84ac3793.svg);
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
}

.search-box {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  height: 45px;
  width: 100%;
  border: 1px solid #bababa;
  box-sizing: border-box;
  border-radius: 10px;
}

.text2 {
  display: flex;
  justify-content: center;
  font-family: "DM Sans";
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  color: #ffffff;
}

.forgot {
  font-weight: bold;
}

.sign-up {
  font-weight: bold;
  color: #0acf83;
}

.container {
  width: 155px;
  height: 213px;
  background-color: #ffffff;
  justify-content: center;
  border-radius: 15px;
  margin: 7px;
}
.wrapper-2 {
  font-family: "DM Sans";
  font-style: normal;
  letter-spacing: 0.2px;
}

.product-name {
  font-size: 14px;
  line-height: 18px;
}

.price {
  font-weight: bold;
  font-size: 12px;
  line-height: 16px;
}

.title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 24px;
  font-family: "DM Sans";
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  height: 55px;
  width: 100%;
}

.icon {
  cursor: "pointer";
}

.badge {
  display: flex;
  align-items: center;
  justify-content: center;
  position: static;
  height: 25px;
  margin: 10px;
  font-family: "DM Sans";
  font-size: 14px;
  line-height: 20px;
  cursor: pointer;
}

.true {
  background: #0acf83;
  border-radius: 30px;
  color: #ffffff;
  padding-left: 15px;
  padding-right: 15px;
}

.false {
  color: #7f7f7f;
}

.banner {
  display: flex;
  margin: 7px;
  height: 178px;
  background-color: #ffffff;
  border-radius: 10px;
}

.wrapper {
  display: flex;
  margin: 20px;
}

.title {
  display: flex;
}

.text {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  color: #0acf83;
  margin-right: 12px;
}

.wrapper-3 {
  display: flex;
}

.container {
  width: 100%;
  margin: 0, 24px;
}

.container-2 {
  background-color: #f6f6f6;
  padding-top: 15px;
  border-radius: 30px 30px 0px 0px;
}

.badge {
  display: flex;
  margin: 10px;
  justify-content: flex-start;
}

.search-box {
  font-family: "DM Sans";
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  height: 50px;
  width: 100%;
  border: 1px solid #bababa;
  box-sizing: border-box;
  border-radius: 10px;
  margin-bottom: 20px;
}

